









import SearchBar from "../SearchBar.vue";
import { mapActions, mapGetters, mapMutations } from "vuex";
import Vue from "vue";

export default Vue.extend({
  components: { SearchBar },
  methods: {
    ...mapMutations(["SET_PR_SEARCH_TERM"]),
    ...mapActions(["getPaginatedPrs"]),
    emit(e: string) {
      this.SET_PR_SEARCH_TERM(e);
      this.getPaginatedPrs();
      this.$emit("filter");
    },
  },
  mounted() {
    this.SET_PR_SEARCH_TERM("");
  },
  beforeDestroy() {
    this.SET_PR_SEARCH_TERM("");
  },
  computed: {
    ...mapGetters(["role"]),
  },
});
