<template>
  <div class="repo-list-search-container">
    <input
      @keyup="$emit('search', searchTerm)"
      :style="{ height: height }"
      type="search"
      class="repo-list-search"
      :placeholder="placeholder"
      autocomplete="off"
      :aria-label="placeholder"
      v-model="searchTerm"
      value=""
    />
  </div>
</template>

<script>
export default {
  data: () => ({
    searchTerm: "",
  }),
  props: {
    placeholder: {
      type: String,
      default: "",
    },
    height: {
      type: String,
      default: "55px",
    },
  },
};
</script>

