












import MainLayout from "../components/MainLayout.vue";
import FilterBar from "../components/author/Filter.vue";
import Pagination from "../components/Pagination.vue";
import PRList from "../components/PRList.vue";
import { mapActions, mapGetters } from "vuex";
import Vue from "vue";

export default Vue.extend({
  name: "Author",
  components: {
    MainLayout,
    FilterBar,
    PRList,
    Pagination,
  },
  data: () => ({}),
  methods: {
    ...mapActions(["getPaginatedPrs"]),
    newPR() {
      this.navigate("compare");
    },
    navigate(view: string) {
      this.$router.push({
        path: `/${this.$route.params.repoUrl}/${view}`,
      });
    },
    changePage(pageNo: number) {
      this.getPaginatedPrs(pageNo);
      document.body.scrollTop = document.documentElement.scrollTop = 0;
    },
  },
  computed: {
    ...mapGetters(["user", "prs", "paginationLength"]),
    length(): number {
      return Math.ceil(this.prs.length / this.paginationLength);
    },
  },
  mounted() {
    window.scrollTo(0, 0);
  },
});
