<template>
  <div class="pagination">
    <v-pagination v-model="page" dark :length="length" @input="next"></v-pagination>
  </div>
</template>

<script>
export default {
  name: "Pagination.vue",
  data: () => ({
    page: 1,
  }),
  methods: {
    next(page) {
      this.$emit("input", page);
    },
    reset() {
      this.page = 1;
    },
  },
  props: {
    length: {
      type: Number,
      default: 1,
    },
  },
};
</script>

<style scoped></style>
