
















import moment from "moment";
import Vue from "vue";

export default Vue.extend({
  props: {
    pr: {
      type: Object,
    },
  },
  methods: {
    format(e: string) {
      return moment(e).format("MMMM Do, YYYY");
    },
    navigate() {
      this.$router.push({
        path: `/${this.$route.params.repoUrl}/pulls/${this.pr.number}`,
      });
    },
  },
});
