<template>
  <div v-if="prs.length > 0">
    <PRListItem v-for="i in prs" :key="i.id" :pr="i" />
  </div>
  <div v-else class="empty-list"><p class="empty-list-msg">No matching pull request found :(</p></div>
</template>

<script>
import PRListItem from "./PRListItem.vue";
import { mapActions, mapGetters } from "vuex";
export default {
  components: { PRListItem },
  props: {
    searchTerm: {
      type: String,
      default: "",
    },
  },
  methods: {
    ...mapActions(["getRepoPullRequests", "getPaginatedPrs"]),
  },
  computed: {
    ...mapGetters({
      prs: "paginatedPrs",
    }),
    filterByTerm() {
      return this.prs.filter((item) => {
        return item.title.toLowerCase().includes(this.searchTerm);
      });
    },
  },
  async mounted() {
    await this.getRepoPullRequests(this.$route.params.repoUrl);
    this.getPaginatedPrs();
  },
};
</script>

